/* General Reset and Font */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
  background-color: #f4f4f9;
  color: #333;
}

.app-container {
  max-width: 600px;
  margin: 30px auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.1);
  transition: 0.3s;
}

.app-container:hover {
  box-shadow: 0px 15px 30px rgba(0, 0, 0, 0.15);
}

.header h1 {
  text-align: center;
  color: #4a90e2;
  font-size: 2rem;
  margin-bottom: 20px;
}

.expense-form {
  margin-bottom: 20px;
}

.input-group {
  display: flex;
  justify-content: space-between;
}

.input {
  flex: 1;
  margin-right: 10px;
  padding: 10px;
  border: 2px solid #eaeaea;
  border-radius: 5px;
  font-size: 1rem;
  transition: border-color 0.2s ease;
}

.input:focus {
  border-color: #4a90e2;
  outline: none;
}

.btn-add {
  padding: 10px 20px;
  background-color: #4a90e2;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.btn-add:hover {
  background-color: #357ab8;
}

.expense-list {
  margin-top: 30px;
}

.expense-list h2 {
  text-align: center;
  color: #333;
  margin-bottom: 10px;
}

ul {
  list-style: none;
}

.expense-item {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  background-color: #f9f9f9;
  margin-bottom: 10px;
  border-radius: 5px;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.1);
  transition: 0.3s;
}

.expense-item:hover {
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
}

.btn-delete {
  background-color: transparent;
  border: none;
  color: #ff4d4d;
  font-size: 1.2rem;
  cursor: pointer;
  transition: color 0.2s ease;
}

.btn-delete:hover {
  color: #d12f2f;
}

.total {
  text-align: center;
  margin-top: 30px;
}

.total h3 {
  font-size: 1.5rem;
  color: #4a90e2;
}

/* Media Queries for Responsiveness */
@media (max-width: 768px) {
  .app-container {
    padding: 15px;
    width: 90%;
  }

  .input-group {
    flex-direction: column;
  }

  .input {
    margin-right: 0;
    margin-bottom: 10px;
  }

  .btn-add {
    width: 100%;
  }
}
